import React, { FC, useEffect, useRef, useState } from "react";
import { PolyIconProps } from "./Polygon.types";
import { useIcon } from "./Polygon.methods";
import { Polygon } from "./Polygon";
import { apothem, aspect, divide, rad, subtract, XY } from "../../geometry";

export const PolyIcon: FC<PolyIconProps> = ({
	icon: Icon,
	iconScaleFactor = 0.8,
	iconPosition = [0.2, 0.1],
	sides = 6,
	rotation = 0,
	...polygonProps
}) => {
	const dimensions = aspect(sides, rad(rotation));
	const apx = apothem(sides);
	const difference = subtract(dimensions, apx);
	const ref = useIcon(Icon, iconScaleFactor, apothem(sides), difference as XY);
	return (<Polygon {...{sides, rotation, ...polygonProps}} >
		<g ref={ref}>
			<circle cx={0} cy={0} r={apx} fill='yellow'/>
		</g>
	</Polygon>);
}